import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Button,
  Divider,
  IconButton,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  Snackbar,
  SnackbarContent,
} from "@material-ui/core";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { ArrowBack, Close, Add } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../../api/api";
import { logout } from "../../actions";

const FAQ = (props) => {
  const { logout, history } = props;
  const [formvalues, setformvalues] = useState({
    id: "",
    question: "",
    answer: "",
    code: "",
  });

  const [wait, setwait] = useState(false);
  const [createdialog, setcreatedialog] = useState(false);
  const [editdialog, seteditdialog] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);
  const [faq, setFaq] = useState([]);

  useEffect(() => {
    document.title = "FAQ";
    fetchFAQ();
  }, []);

  const fetchFAQ = async () => {
    try {
      const response = await api.get(`/api/faq`);
      return setFaq(response.data.data);
    } catch (err) {
      return;
    }
  };

  const submit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);

        const response = await api.post(`/api/faq`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });

        setFaq([
          ...faq,
          {
            ...formvalues,
            id: response.data.id,
          },
        ]);
        setformvalues({
          id: "",
          question: "",
          answer: "",
          code: "",
        });
        setstatus(green[700]);
        setmessage("FAQ Published Successfully");
        setwait(false);
        setcreatedialog(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const submitEdit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.patch(`/api/faq/${formvalues.id}`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });
        setFaq(faq.map((a) => (a.id === formvalues.id ? formvalues : a)));
        setformvalues({
          id: "",
          question: "",
          answer: "",
          code: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("FAQ Updated Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const deletesubmit = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.delete(
          `/api/faq/${formvalues.id}`,

          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );
        setFaq(faq.filter((a) => a.id !== formvalues.id));
        setformvalues({
          id: "",
          question: "",
          answer: "",
          code: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("FAQ Deleted Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const renderlist = () => {
    if (faq.length) {
      return (
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell>Code</TableCell>
              <TableCell>Question</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {faq.map((a, index) => {
              return (
                <TableRow
                  hover
                  key={index}
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setformvalues({ ...a });
                    seteditdialog(true);
                  }}
                >
                  <TableCell>
                    <Typography>{a.code}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.question}</Typography>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 300 }}>
          NO DATA TO DISPLAY!
        </Typography>
      );
    }
  };

  const renderdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={createdialog}
      onClose={() => {
        setformvalues({
          id: "",
          question: "",
          answer: "",
          code: "",
        });
        setcreatedialog(false);
      }}
    >
      <DialogTitle>Publish FAQ</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submit}>
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            type="number"
            size="small"
            label="Code (For sorting)"
            value={formvalues.code}
            onChange={(e) =>
              setformvalues({ ...formvalues, code: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Question"
            value={formvalues.question}
            onChange={(e) =>
              setformvalues({ ...formvalues, question: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Answer"
            multiline
            rows={5}
            value={formvalues.answer}
            onChange={(e) =>
              setformvalues({ ...formvalues, answer: e.target.value })
            }
          />

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Submit
            </Button>
            <Button
              variant="contained"
              onClick={() => {
                setformvalues({
                  id: "",
                  question: "",
                  answer: "",
                  code: "",
                });
                setcreatedialog(false);
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  const rendereditdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={editdialog}
      onClose={() => {
        setformvalues({
          id: "",
          question: "",
          answer: "",
          code: "",
        });
        seteditdialog(false);
      }}
    >
      <DialogTitle>Edit FAQ</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submitEdit}>
        <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            type="number"
            size="small"
            label="Code (For sorting)"
            value={formvalues.code}
            onChange={(e) =>
              setformvalues({ ...formvalues, code: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Question"
            value={formvalues.question}
            onChange={(e) =>
              setformvalues({ ...formvalues, question: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Answer"
            multiline
            rows={5}
            value={formvalues.answer}
            onChange={(e) =>
              setformvalues({ ...formvalues, answer: e.target.value })
            }
          />

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Update
            </Button>
            <Button
              disabled={wait}
              style={{ marginRight: 10 }}
              color="secondary"
              variant="contained"
              onClick={() => {
                if (
                  window.confirm(
                    `Are you sure you want to delete this FAQ? You cannot recover once deleted!`
                  )
                ) {
                  return deletesubmit();
                }
              }}
            >
              Delete
            </Button>

            <Button
              variant="contained"
              onClick={() => {
                setformvalues({
                  id: "",
                  question: "",
                  answer: "",
                  code: "",
                });
                seteditdialog(false);
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  return (
    <Container>
      <Typography variant="h6">
        <IconButton
          edge="start"
          onClick={() => {
            history.goBack();
          }}
        >
          <ArrowBack />
        </IconButton>
        Frequently Ask Questions Management
      </Typography>
      <div style={{ display: "block", marginTop: 8 }}>
        <Button
          variant="contained"
          color="secondary"
          startIcon={<Add />}
          onClick={() => setcreatedialog(true)}
        >
          Post New FAQ
        </Button>
      </div>
      <Divider style={{ marginTop: 20 }} />
      {renderlist()}
      {renderdialog()}
      {rendereditdialog()}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

export default connect(null, { logout })(withRouter(FAQ));
